<template>
  <div class="home px-5 py-10">
    <img src="../assets/images/banner-wheel.svg" class=" mx-auto max-w-[500px] mb-10" alt="">
    <div class="wheel relative flex justify-center items-center">
      <img src="../assets/images/arrow.png" class="absolute w-[95px] z-20"  alt="">
      <img src="../assets/images/wheelEat.png" class="wheel-bg w-[360px] z-10" alt="">
    </div>
    <div class="flex justify-around items-center mt-10">
      <button :disabled="isAgain" :class="{'bg-[#ffbe04]': !isAgain, 'bg-[#858585]': isAgain}" class="start-btn w-2/5 mx-auto py-3 text-2xl font-bold tracking-widest text-white" @click="AgainWheel()">重來一次</button>
      <button :disabled="isStart" :class="{'bg-[#ff4a3f]': !isStart, 'bg-[#858585]': isStart}" class="start-btn w-2/5 mx-auto py-3 text-2xl font-bold tracking-widest text-white" @click="start()">開始</button>
    </div>
  </div>
  <div class="modal flex items-center justify-center" v-if="showReward || notice || showRules || showRecord" :style="{ height: `${windowHeight}px` }">
    <div class="reward flex flex-col items-center justify-center relative" v-if="showReward">
      <div>
        <p class="text-[#323232] text-2xl font-bold">{{rewadList[randomNumber - 1].rewardName}}</p>
        <img :src="rewadList[randomNumber - 1].rewardImg" class="w-4/5 mx-auto" alt="">
      </div>
      <button @click="showReward = false" class="close-button absolute bottom-5 bg-[#8a8a87] text-white py-2 px-5 rounded-2xl font-bold tracking-widest">關閉視窗</button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import axios from 'axios'
// import CryptoJS from 'crypto-js'
// import axios from 'axios'

export default {
  name: 'HomeView',
  components: {
    // HelloWorld
  },
  data () {
    return {
      isStart: false,
      showReward: false,
      notice: false,
      showRules: false,
      isAgain: true,
      randomNumber: 0,
      windowHeight: window.innerHeight,
      rewadList: [
        { rewardName: '速食餐廳增肥去', rewardImg: require('../assets/images/burger.gif') },
        { rewardName: '開心吃披薩', rewardImg: require('../assets/images/pizza.gif') },
        { rewardName: '暖呼呼的火鍋', rewardImg: require('../assets/images/hot-pot.gif') },
        { rewardName: '來點牛肉麵或拉麵', rewardImg: require('../assets/images/ramen.gif') },
        { rewardName: '吃個餃子或小籠包(鼎泰豐~~讚)', rewardImg: require('../assets/images/dumplings.gif') },
        { rewardName: '來點飯類主食~咖哩飯、炒飯等', rewardImg: require('../assets/images/curry.gif') }
      ]
    }
  },
  created () {
  },
  mounted () {
    // 註冊 resize 事件
    window.addEventListener('resize', this.updateWindowHeight)
  },
  methods: {
    updateWindowHeight () {
      this.windowHeight = window.innerHeight
    },
    start () {
      this.isStart = true
      this.randomNumber = Math.floor(Math.random() * 6) + 1
      const randomOffset = Math.floor(Math.random() * (56 - 3 + 1)) + 3
      const rotate = (5 * 360) + ((this.randomNumber - 1) * 360 / 6) - 30 + randomOffset // 先-30貼齊線後再加上偏移值3~56
      console.log('randomNumber,rotate', this.randomNumber, rotate)
      document.querySelector('.wheel-bg').style.transform = `rotate(${rotate}deg)`
      document.querySelector('.wheel-bg').style.transition = 'transform 5s ease-in-out'
      setTimeout(() => {
        this.showReward = true
        this.isAgain = false
      }, 5500)
    },
    AgainWheel () {
      document.querySelector('.wheel-bg').style.transform = 'rotate(0deg)'
      document.querySelector('.wheel-bg').style.transition = 'none'
      this.isStart = false
      this.isAgain = true
    }
  }
}
</script>
